[class*="Clubs"],
[class*="Spades"],
[class*="Hearts"],
[class*="Diamonds"] {
  display: inline-block;
  width: 72px;
  height: 98px;
  margin-top: 1em;
  margin-right: .3em;
  vertical-align: text-top;
  background-image: url("../../assets/playingCards.png");
  background-repeat: no-repeat;
}

.cardBack {
  display: inline-block;
  width: 72px;
  height: 98px;
  margin-top: 1em;
  margin-right: .3em;
  vertical-align: text-top;
  background-image: url("../../assets/cardBack.png");
  background-repeat: no-repeat;
}

